.vidAll{
    margin: 20px 10px 5px 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;

}
.divvideoItem  {
    display: block;
    text-decoration: none;
    overflow: hidden;
    vertical-align: top;
    position: relative;
    float: left;
    margin: 2px 10px 10px 5px;
    padding: 10px  10px;
    background: rgba(99%, 99%, 99%, 0.2);
    border-radius: 10px;

}

.videoplaer video {
    overflow: hidden;
    width: 400px;
    height: 300px;
    top: 0;
    border-radius: 10px;
}
.divvideoItem span {
    display: block;
    width: 400px;
    font-size: 20px;
    font-weight: bold;
    font-family: Andalus;
}

.divvideoItem:hover{
    background: rgba(99%, 99%, 99%, 0.5);

}