.Foto{
    margin: 20px 10px 5px 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

}
.fotoitem {
    display: block;
    height: 400px;
    width: 400px;
    overflow: hidden;
    vertical-align: top;
    position: relative;
    float: left;
    padding: 5px;
    margin: 2px 2px 2px 5px;
    background: rgba(99%, 99%, 99%, 0.1);

}
.fotoitem   img{
    overflow: hidden;
    width: 90%;
    height: 90%;
    border: white solid 1px;
    border-radius: 5%;

}
.fotoitem span {
    height: 10%;
    margin: 0px  5px 50px 5px ;
    padding: 0;
    text-decoration: none;
    color: white;
    font-weight: bold;
    font-size: 20px;

}
.fotoitem:hover{
    background: rgba(99%, 99%, 99%, 0.3);

}