* {
    box-sizing: border-box
}
.uperfoter, .loverfoter{
    height: 10vh;
    width: 100vw;
    /*z-index: 99;*/
}
.uperfoter{
    text-align: center;
}
.countdown {
    font-size: 1rem;
    z-index: 10
}
.countdown a {
    display: inline-block;
    padding: .5rem;
    line-height: 0;
    background: radial-gradient(circle 1rem, rgba(239, 168, 79, 4), rgba(235, 161, 26, 0))
}
.countdown a img {
    width: 2rem;
    height: 2rem
}
.LoverFooter {
    font-size: 1rem;
    z-index: 99
}
.LoverFooter a {
    /*display: inline-block;*/
    padding: .5rem;
    line-height: 0;
    background: radial-gradient(circle 1rem, rgba(239, 168, 79, 4), rgba(235, 161, 26, 0))
}
.LoverFooter a img {
    width: 3rem;
    height: 2rem
}











